.container {
	border: solid 3px #aaa;
	border-radius: 10px;
	height: 50px;
	padding: 3px 5px 0;;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	margin-top: 5px;
}

.name{

	text-align: left;
	flex-grow: 1;
}

.foto{
	width: 50px;
}

img{
	width: 100%;
}

.summ{
	margin: 0 10px;
}

.plus{
	width: 20px;
	height: 20px;
	font-size: 24px;
	line-height: 17px;
	border: solid 3px #aaa;
	color: #aaa;
	text-align: center;
	border-radius: 50%;
}